/* src/ScrollShowcase.css */

@import url('https://fonts.googleapis.com/css2?family=Geist:wght@100..900&display=swap');

html, body {
  margin: 0;
  padding: 0;
}

/* controla altura e espaçamento para não sobrepor */
.word-section {
  position: relative;
}

/* fixa a headline no mesmo sítio enquanto durar o scroll */
.headline-container {
  position: sticky;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  pointer-events: none;
  z-index: 10;
  width: 100%;
}

/* estilos do texto, agora maior */
.headline-container .static,
.headline-container .dynamic {
  font-family: 'Geist', sans-serif;
  font-weight: 600;
  font-size: clamp(3.5rem, 8vw, 6rem);
  line-height: 1.1;
  margin: 0;
  color: #000;
}



